@font-face {
  font-family: 'Timeless';
  src: url("../fonts/Timeless/hinted-Timeless-Normal.eot");
  src: local("Timeless-Normal"), url("../fonts/Timeless/hinted-Timeless-Normal.eot?#iefix") format("embedded-opentype"), url("../fonts/Timeless/hinted-Timeless-Normal.woff") format("woff"), url("../fonts/Timeless/hinted-Timeless-Normal.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Timeless Bold';
  src: url("../fonts/Timeless/hinted-Timeless-Bold.eot");
  src: local("Timeless-Bold"), url("../fonts/Timeless/hinted-Timeless-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Timeless/hinted-Timeless-Bold.woff") format("woff"), url("../fonts/Timeless/hinted-Timeless-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Gill Sans';
  src: url("../fonts/Gillsans/GillSans.eot");
  src: local("GillSans"), url("../fonts/Gillsans/GillSans.eot?#iefix") format("embedded-opentype"), url("../fonts/Gillsans/GillSans.woff") format("woff"), url("../fonts/Gillsans/GillSans.ttf") format("truetype");
  font-weight: 100;
  font-style: normal;
}

@font-face {
  font-family: 'Gill Sans Italic';
  src: url("../fonts/Gillsans/hinted-GillSans-Italic.eot");
  src: local("Gill Sans Italic"), local("GillSans-Italic"), url("../fonts/Gillsans/hinted-GillSans-Italic.eot?#iefix") format("embedded-opentype"), url("../fonts/Gillsans/hinted-GillSans-Italic.woff") format("woff"), url("../fonts/Gillsans/hinted-GillSans-Italic.ttf") format("truetype");
  font-weight: normal;
  font-style: italic;
}

html {
  font-size: 62.5%;
  height: 100%;
}

@media screen and (min-width: 1780px) and (max-width: 1919px) {
  html {
    font-size: 58%;
  }
}

@media screen and (min-width: 1680px) and (max-width: 1779px) {
  html {
    font-size: 55%;
  }
}

@media screen and (min-width: 1580px) and (max-width: 1679px) {
  html {
    font-size: 52%;
  }
}

@media screen and (min-width: 1480px) and (max-width: 1579px) {
  html {
    font-size: 50%;
  }
}

@media screen and (max-width: 1479px) {
  html {
    font-size: 48%;
  }
}

body {
  font-family: 'Gill Sans';
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #fff;
  font-size: 14px;
  font-size: 1.4rem;
  overflow-x: hidden;
}

h1 {
  font-size: 78px;
  font-size: 7.8rem;
}

@media screen and (max-width: 768px) {
  h1 {
    font-size: 5.5rem;
  }
}

@media screen and (max-width: 767px) {
  h1 {
    font-size: 31px;
  }
}

h2 {
  font-size: 3.9rem;
}

@media screen and (max-width: 768px) {
  h2 {
    font-size: 3.5rem;
  }
}

@media screen and (max-width: 767px) {
  h2 {
    font-size: 25px;
  }
}

h3 {
  font-size: 30px;
  font-size: 3rem;
}

@media screen and (max-width: 768px) {
  h3 {
    font-size: 2.8rem;
  }
}

@media screen and (max-width: 767px) {
  h3 {
    font-size: 22px;
  }
}

h4 {
  font-size: 23px;
  font-size: 2.3rem;
}

h5 {
  font-size: 19px;
  font-size: 1.9rem;
}

h6 {
  font-size: 13px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Timeless';
  line-height: 1.1;
  color: #323232;
}

p, li {
  font-size: 16px;
  line-height: 1.5;
  color: #8f8f8f;
  font-family: 'Gill Sans';
}

@media screen and (max-width: 1400px) {
  p, li {
    font-size: 14px;
  }
}

.title-bold {
  font-family: 'Timeless Bold';
}

/* =========================

        GENERAL STYLES

   =========================*/
*, *:before, *:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: none;
}

a:link,
a:visited,
a:active {
  color: #fff;
  pointer-events: auto;
  text-decoration: none;
}

a:link:hover,
a:visited:hover,
a:active:hover {
  color: #dbdbdb;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}

.button-navigation-wrapper {
  position: fixed;
  right: 0;
  left: auto;
  z-index: 5;
}

.menu-icon {
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
}

.menu-icon .bar {
  position: relative;
  display: block;
  height: 2px;
  width: 42px;
  background-color: #fff;
  margin-bottom: 8px;
  -webkit-transition: width 0.3s ease;
  transition: width 0.3s ease;
  transform: translate3d(0, 0, 0);
  margin-left: auto;
}

.menu-icon .bar:last-child {
  width: 25px;
  margin-bottom: 0;
}

.menu-icon-wrapper {
  background-color: #330b0b;
  border: none;
  box-shadow: none;
  outline: none;
  padding: 32px 22px;
}

@media screen and (max-width: 1400px) {
  .menu-icon-wrapper {
    padding: 28px 18px;
  }
}

.menu-icon-wrapper:focus, .menu-icon-wrapper:hover {
  box-shadow: none;
  outline: none;
}

button.book-button {
  position: relative;
  display: block;
  background-color: #f1ebd9;
  color: #330b0b;
  font-family: 'Timeless Bold';
  font-size: 19px;
  padding: 3px;
}

@media screen and (max-width: 1400px) {
  button.book-button {
    font-size: 17px;
  }
}

button.book-button .book-button-inner {
  width: 80px;
  height: 78px;
  line-height: 78px;
  border: 1px solid #cfc083;
  background-color: transparent;
}

@media screen and (max-width: 1400px) {
  button.book-button .book-button-inner {
    width: 72px;
    height: 70px;
    line-height: 70px;
  }
}

.menu-icon-wrapper:hover .bar,
.menu-icon-wrapper.active .bar {
  width: 42px !important;
}

.menu-popup-wrapper {
  position: fixed;
  text-align: center;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  min-height: 450px;
  z-index: 15;
  overflow-y: auto;
  z-index: 1040;
  pointer-events: none;
  overflow: hidden;
}

.menu-popup-wrapper.active {
  pointer-events: auto;
}

.menu-popup-overlay {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.menu-popup {
  position: relative;
  float: right;
  height: 100%;
  width: 41%;
  min-width: 575px;
  background: #571c1c;
  /* The Fallback */
  background: rgba(87, 28, 28, 0.95);
  transform: translate3d(100%, 0, 0);
  transition: transform 0.4s ease;
}

.menu-popup-wrapper.active .menu-popup {
  transform: translate3d(0, 0, 0);
}

ul.select-language {
  position: absolute;
  top: 3%;
  left: 8%;
  padding-left: 0;
}

ul.select-language > li {
  position: relative;
  display: inline-block;
  padding: 8px 5px;
  color: #fff;
  margin-right: 30px;
  cursor: pointer;
}

ul.select-language > li:before {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: transparent;
  transition: background-color 0.4s ease;
}

ul.select-language > li:hover:before, ul.select-language > li.active:before {
  background-color: #fff;
}

.menu-list-wrapper {
  display: flex;
  height: 100%;
  align-items: center;
}

.menu-list {
  color: #fff;
  text-align: right;
  list-style: none;
  margin-left: auto;
  margin-right: 17%;
}

.menu-list > li {
  position: relative;
  color: #fff;
  font-size: 60px;
  font-size: 6rem;
  text-transform: uppercase;
  font-family: 'Timeless';
  padding: 6.5% 0;
  transition: color 0.4s ease;
  cursor: pointer;
  float: right;
  clear: right;
}

@media screen and (max-width: 1400px) {
  .menu-list > li {
    font-size: 43px;
    font-size: 4.3rem;
  }
}

.menu-list > li:after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  right: -30px;
  transform: translate3d(0, -50%, 0);
  height: 2px;
  width: 106%;
  width: calc(100% + 20px);
  background: #cfc083;
  /* The Fallback */
  background: rgba(207, 192, 131, 0);
  transition: background-color 0.4s ease;
}

.menu-list > li.active, .menu-list > li:hover {
  color: #cfc083;
}

.menu-list > li.active:after, .menu-list > li:hover:after {
  background: #cfc083;
  /* The Fallback */
  background: #cfc083;
}

.menu-list > li.active .hover-line, .menu-list > li:hover .hover-line {
  opacity: 1;
}

.menu-list > li .hover-line {
  opacity: 0;
  transition: opacity 0.4s ease;
  position: absolute;
  height: 18px;
  width: 18px;
  border-radius: 0;
  border: 2px solid #cfc083;
  top: 50%;
  right: -50px;
  transform-origin: 50% 0%;
  transform: rotate(45deg) translate3d(0, -50%, 0);
  background: #571c1c;
  /* The Fallback */
  background: rgba(87, 28, 28, 0.95);
}

.menu-list > li .hover-line:after {
  content: '';
  display: block;
  position: absolute;
  width: 5px;
  height: 5px;
  background-color: #cfc083;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.popup-footer-links {
  position: absolute;
  bottom: 5%;
  width: 100%;
  padding-right: 17%;
  padding-left: 8%;
  text-align: right;
}

.popup-footer-links ul {
  display: inline-block;
  vertical-align: middle;
  list-style: none;
  margin-bottom: 0;
  margin-right: 8%;
  padding-left: 0;
}

.popup-footer-links ul > li {
  position: relative;
  display: inline-block;
  color: #eaeaea;
  padding: 0 25px;
  padding: 0 2.5rem;
  font-size: 18px;
  font-size: 1.8rem;
}

.popup-footer-links ul > li:after {
  content: '';
  display: block;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate3d(0, -50%, 0);
  height: 15px;
  width: 1px;
  background-color: #fff;
}

.popup-footer-links ul > li:last-child:after {
  content: none;
}

.popup-footer-links .popup-social-media {
  display: inline-block;
  vertical-align: middle;
}

.popup-footer-links .popup-social-media .fa {
  position: relative;
  color: #eaeaea;
  padding: 0 20px;
  font-size: 18px;
  font-size: 1.8rem;
}

.popup-footer-links .popup-social-media .fa:after {
  content: '';
  display: block;
  height: 15px;
  width: 1px;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate3d(0, -50%, 0);
  background-color: #fff;
}

.popup-footer-links .popup-social-media .fa:last-child {
  padding-right: 0;
}

.popup-footer-links .popup-social-media .fa:last-child:after {
  content: none;
}

.close-button {
  position: absolute;
  right: 0;
  top: 0;
  padding: 20px;
  border-radius: 0;
  background-color: #330b0b;
  border: none;
  box-shadow: none;
  outline: none !important;
}

@media screen and (max-width: 1400px) {
  .close-button {
    padding: 16px;
  }
}

@media screen and (max-width: 767px) {
  .close-button {
    top: 15px;
    right: 15px;
  }
}

.close-button:hover, .close-button:focus, .close-button.focus, .close-button:active {
  background-color: #330b0b;
  border: none;
  box-shadow: none;
  outline: none !important;
}

.close-button .close-mark {
  position: relative;
  width: 40px;
  height: 40px;
  top: 0;
  right: 0;
}

.close-button .close-mark:before, .close-button .close-mark:after {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  content: ' ';
  height: 40px;
  width: 2px;
  background-color: #fff;
}

.close-button .close-mark:before {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.close-button .close-mark:after {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.close-button .close-mark .close-diamond {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0) rotate(45deg);
  width: 13px;
  height: 13px;
  background-color: #fff;
  border: 3px solid #330b0b;
  z-index: 2;
}

.modal-dialog-custom {
  width: 100%;
  height: 100%;
  margin: 0;
  min-height: 400px;
}

.modal-dialog-custom button.close-button.inverted {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #f1ebd9;
  z-index: 1;
}

.modal-dialog-custom button.close-button.inverted .close-diamond {
  background-color: #330b0b;
  border: 3px solid #f1ebd9;
}

.modal-dialog-custom button.close-button.inverted .close-mark:before, .modal-dialog-custom button.close-button.inverted .close-mark:after {
  background-color: #330b0b;
}

.modal-dialog-custom .modal-content-custom {
  height: 100%;
  border-radius: 0;
  background-image: url(../../assets/images/main/popup-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.modal-dialog-custom .modal-body-custom {
  height: 100%;
  padding: 0;
}

.modal-dialog-custom .book-a-table-logo-wrapper {
  position: relative;
  float: left;
  width: 42%;
  height: 100%;
}

.modal-dialog-custom .book-a-table-logo-wrapper:before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../../assets/images/main/pattern-transparent.png) center/auto repeat, radial-gradient(#571c1c, #330b0b);
  background: url(../../assets/images/main/pattern-transparent.png) center/auto repeat, radial-gradient(rgba(87, 28, 28, 0.88), rgba(51, 11, 11, 0.88));
  mix-blend-mode: multiply;
}

.modal-dialog-custom .book-a-table-logo-wrapper .logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 35%;
}

.modal-dialog-custom .book-a-table-form-wrapper {
  position: relative;
  float: right;
  background-color: rgba(24, 24, 24, 0.96);
  height: 100%;
  text-align: center;
  padding: 5% 10%;
  width: 58%;
  display: flex;
  align-items: center;
}

.modal-dialog-custom .book-a-table-form-wrapper .book-a-table-container {
  position: relative;
  width: 100%;
}

.modal-dialog-custom .book-a-table-form-wrapper h2 {
  text-transform: uppercase;
  color: #cfc083;
  font-size: 48px;
  font-size: 4.8rem;
}

#header {
  position: relative;
  width: 100%;
  height: 100%;
  height: 107%;
  min-height: 400px;
  background-color: rgba(51, 11, 11, 0.5);
  background-blend-mode: multiply;
  background-size: cover;
  background-position: center;
}

#header video {
  display: block;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#header img.logo {
  position: absolute;
  width: 220px;
  width: 22rem;
  top: 5%;
  left: 7%;
  transform: translate3d(0, 0, 0);
  z-index: 1;
}

@media screen and (max-width: 1400px) {
  #header img.logo {
    width: 180px;
    width: 18rem;
  }
}

#header .header-pattern {
  position: absolute;
  top: 0;
  right: 0;
  width: 42%;
  height: 100%;
  display: flex;
  align-items: center;
}

#header .header-pattern:before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-image: url(../../assets/images/main/pattern-transparent.png);
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0.4;
  background-position: center left;
}

#header .header-center {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 7%;
  background-color: rgba(51, 11, 11, 0.5);
  background-blend-mode: multiply;
}

#header .header-center h1 {
  text-transform: uppercase;
  color: #fff;
  letter-spacing: 4px;
  margin-top: 10px;
}

#header .header-center h3 {
  color: #cfc083;
  letter-spacing: 2px;
  margin-top: 10px;
}

.gold-line {
  display: inline-block;
  height: 1px;
  background-color: #cfc083;
  width: 10rem;
  vertical-align: middle;
  margin-left: 15px;
  float: none;
}

.diamond-gold-line {
  position: relative;
  display: inline-block;
  height: 1px;
  background-color: #cfc083;
  width: 5rem;
  vertical-align: middle;
  margin-left: 18px;
  float: none;
}

.diamond-gold-line.left {
  margin-left: 0;
  margin-right: 18px;
}

.diamond-gold-line:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  width: 8px;
  height: 8px;
  transform: translate3d(0, -50%, 0) rotate(45deg);
  transform-origin: 50% 50%;
  background: #cfc083;
}

.play {
  display: block;
  width: 0;
  height: 0;
  border-top: 3rem solid transparent;
  border-bottom: 3rem solid transparent;
  border-left: 3.5rem solid #fff;
  margin: 10rem auto 10rem auto;
  position: relative;
  z-index: 1;
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  left: 10px;
}

.play:before {
  content: '';
  position: absolute;
  top: -7.5rem;
  left: -10rem;
  bottom: -7.5rem;
  right: -5rem;
  border: 1px solid #fff;
  z-index: 2;
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transform: rotate3d(0, 0, 1, 45deg);
}

.play:after {
  content: '';
  opacity: 0;
  transition: opacity 0.6s;
  -webkit-transition: opacity 0.6s;
  -moz-transition: opacity 0.6s;
}

.play:hover:before, .play:focus:before {
  transform: scale(1.1) rotate3d(0, 0, 1, 45deg);
}

.box-white-animate {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: #fff;
}

.image-overlay-dark {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(17, 17, 17, 0.08);
}

.btn-custom {
  background-color: #cfc083;
  text-transform: uppercase;
  color: #fff;
  border-radius: 0;
  font-size: 16px;
  font-family: 'Source Sans Pro Semibold';
  padding: 10px 36px;
  line-height: 1.3333333;
  letter-spacing: 1px;
}

@media screen and (max-width: 1400px) {
  .btn-custom {
    padding: 6px 24px;
    font-size: 14px;
  }
}

.btn-custom .arrow-button {
  position: relative;
  width: 18px;
  margin-left: 10px;
  display: inline-block;
  vertical-align: middle;
}

.btn-custom .arrow-button.arrow-button-back {
  margin-left: 0;
  margin-right: 10px;
}

.btn-custom.btn-custom-inline {
  display: inline-block;
  vertical-align: middle;
}

.btn-arrow {
  background: transparent;
  border-color: #fff;
  color: #330b0b;
  text-transform: uppercase;
  border-radius: 0;
  font-size: 16px;
  padding: 12px 70px 12px 0px;
  font-family: 'Gill Sans';
  overflow: hidden;
  transition: all 0.4s;
  vertical-align: middle;
  position: relative;
  z-index: 1;
  -webkit-backface-visibility: hidden;
  -moz-osx-font-smoothing: grayscale;
}

@media screen and (max-width: 1400px) {
  .btn-arrow {
    font-size: 14px;
  }
}

.btn-arrow:focus, .btn-arrow:hover {
  outline: none;
}

.btn-arrow > span {
  vertical-align: middle;
}

.btn-arrow.btn-arrow-animated {
  overflow: hidden;
  -webkit-transition: background-color 0.3s, border-color 0.3s, color 0.3s;
  transition: background-color 0.3s, border-color 0.3s, color 0.3s;
}

.btn-arrow.btn-arrow-animated > span {
  display: inline-block;
  -webkit-transform: translate3d(10px, 0, 0);
  transform: translate3d(10px, 0, 0);
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  -webkit-transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
  transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
}

.btn-arrow.btn-arrow-animated .arrow-button {
  width: 44px;
  display: inline-block;
  margin-left: 8%;
  margin-right: 8%;
  cursor: pointer;
  transform: translate3d(0, 0, 0);
}

.btn-arrow.btn-arrow-animated .arrow-icon {
  position: absolute;
  right: 10px;
  -webkit-transform: translate3d(200px, 0, 0);
  transform: translate3d(200px, 0, 0);
